import React, { useState, useCallback } from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import { TextInput } from "./TextInput";
import SLACK_URL from "../../webhookConfig";

export default function FormDialog(props) {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [description, setDescription] = useState("");

  const imputName = useCallback(
    (event) => {
      setName(event.target.value); //ÂÖ•Âäõ„Åï„Çå„ÅüÂÄ§„ÇíÈöèÊôÇÊõ¥Êñ∞„Åó„Å¶„ÅÑ„Åè
    },
    [setName]
  );

  const imputEmail = useCallback(
    (event) => {
      setEmail(event.target.value);
    },
    [setEmail]
  );

  const imputDescription = useCallback(
    (event) => {
      setDescription(event.target.value);
    },
    [setDescription]
  );

  const emailFormat = (email) => {
    const regex = /^[a-zA-Z0-9.!#$%&'*+\/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
    return regex.test(email);
  };

  const validateRequiredInput = (...args) => {
    let isBlank = false;
    for (let i = 0; i < args.length; i = (i + 1) | 0) {
      if (args[i] === "") {
        isBlank = true;
      }
    }
    return isBlank;
  };

  const submit = () => {
    const isBlank = validateRequiredInput(name, email, description);
    const isValidEmail = emailFormat(email);

    if (isBlank) {
      alert("ÂøÖÈ†àÂÖ•ÂäõÊ¨Ñ„ÅåÁ©∫ÁôΩ„Åß„Åô„ÄÇ");
      return false;
    } else if (!isValidEmail) {
      alert("„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ„ÅÆÊõ∏Âºè„ÅåÁï∞„Å™„Çä„Åæ„Åô„ÄÇ");
      return false;
    } else {
      const payload = {
        text:
          "„ÅäÂïè„ÅÑÂêà„Çè„Åõ„Åå„ÅÇ„Çä„Åæ„Åó„Åü\n" +
          "„ÅäÂêçÂâç: " +
          name +
          "\n" +
          "„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ: " +
          email +
          "\n" +
          "„ÄêÂïè„ÅÑÂêà„Çè„ÅõÂÜÖÂÆπ„Äë\n" +
          description,
      };

      fetch(SLACK_URL.slack, {
        method: "POST",
        body: JSON.stringify(payload),
      }).then(() => {
        alert("ÈÄÅ‰ø°„ÅåÂÆå‰∫Ü„Åó„Åæ„Åó„Åü„ÄÇ");
        setName("");
        setEmail("");
        setDescription("");
        return props.handleClose();
      });
    }
  };

  return (
    <Dialog
      open={props.open}
      onClose={props.handleClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">„ÅäÂïè„ÅÑÂêà„Çè„Åõ</DialogTitle>
      <DialogContent>
        <TextInput
          label={"„ÅäÂêçÂâç"}
          multiline={false}
          value={name}
          rows={1}
          type={"text"}
          onChange={imputName}
        />
        <TextInput
          label={"„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ"}
          multiline={false}
          value={email}
          rows={1}
          type={"email"}
          onChange={imputEmail}
        />
        <TextInput
          label={"„ÅäÂïè„ÅÑÂêà„Çè„ÅõÂÜÖÂÆπ"}
          multiline={true}
          value={description}
          rows={5}
          type={"text"}
          onChange={imputDescription}
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={props.handleClose} color="primary">
          „Ç≠„É£„É≥„Çª„É´
        </Button>
        <Button onClick={submit} color="primary" autoFocus>
          ÈÄÅ‰ø°
        </Button>
      </DialogActions>
    </Dialog>
  );
}
